.App {
  width: 100vw;
  height: 100vh;
  background: #c2e5cf;
  overflow-x: hidden;
  overflow-y: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 3em 2.5em calc(100vh - 5.5em);
  grid-template-areas: "header header" "search search" "map map"; }

.App.active {
  grid-template-rows: 3em 2.5em 40vh 2fr 2fr 2fr 2fr;
  grid-template-areas: "header header" "search search" "map map" "sign-up poll" "state-senate congress" "wccc council" "wcc police"; }

.header {
  height: 3em;
  grid-area: header;
  background: #004445;
  display: flex; }
  .header img {
    width: 3em; }
  .header div {
    display: flex;
    flex: 1;
    color: #fff;
    font-weight: bold; }
    .header div span {
      margin: auto; }

#geocoder {
  border: .1em solid #cecece;
  grid-area: search; }

.Map {
  grid-area: map; }

.poll {
  grid-area: poll; }

.sign-up {
  grid-area: sign-up; }

.police {
  grid-area: police; }

.wccc {
  grid-area: wccc; }

.wcc {
  grid-area: wcc; }

.state-senate {
  grid-area: state-senate; }

.congress {
  grid-area: congress; }

.item.active {
  font-size: .85em;
  padding: .75em; }
  .item.active div {
    margin: auto; }
  .item.active input {
    max-width: 100%;
    border: none;
    padding: .5em; }
  .item.active button {
    padding: 1em 2em;
    border: none;
    background: #feb70d;
    font-weight: bold;
    cursor: pointer; }

.sign-up .box {
  position: relative; }
  .sign-up .box .phone-valid-alert, .sign-up .box .phone-invalid-alert, .sign-up .box .sending-alert {
    display: none;
    position: absolute;
    top: 0;
    padding: 1.5em; }
  .sign-up .box .sending-alert.active {
    width: 100%;
    padding: 2em;
    display: block;
    background: #feb70d; }
  .sign-up .box .phone-valid-alert.active {
    display: block;
    background: #9fd5b3; }
  .sign-up .box .phone-invalid-alert.active {
    display: block;
    background: #fd9a93; }
  .sign-up .box .close-phone-validation-alert {
    position: absolute;
    cursor: pointer;
    top: -.25em;
    right: -.25em;
    background: #fff;
    color: #f44e42;
    font-size: 1.5em;
    font-weight: bold;
    padding: .05em .4em; }

.important-dates {
  display: none; }

.important-dates.active {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex; }
  .important-dates.active div {
    background: #fff;
    text-align: center;
    padding: 1em;
    margin: auto; }

.item.active {
  border: 0.2em solid rgba(255, 255, 255, 0.5); }

@media all and (min-width: 950px) {
  .App {
    grid-template-columns: 33.33334%;
    grid-template-areas: "header header header" "search search search" "map map map" "sign-up sign-up poll" "congress state-senate council" "wccc wcc police"; }
  .sign-up {
    display: flex; }
    .sign-up .box {
      margin: auto; }
    .sign-up input {
      padding: 1em; } }
