@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif; }

.App {
  width: 100vw;
  height: 100vh;
  background: #c2e5cf;
  overflow-x: hidden;
  overflow-y: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 3em 2.5em calc(100vh - 5.5em);
  grid-template-areas: "header header" "search search" "map map"; }

.App.active {
  grid-template-rows: 3em 2.5em 40vh 2fr 2fr 2fr 2fr;
  grid-template-areas: "header header" "search search" "map map" "sign-up poll" "state-senate congress" "wccc council" "wcc police"; }

.header {
  height: 3em;
  grid-area: header;
  background: #004445;
  display: flex; }
  .header img {
    width: 3em; }
  .header div {
    display: flex;
    flex: 1 1;
    color: #fff;
    font-weight: bold; }
    .header div span {
      margin: auto; }

#geocoder {
  border: .1em solid #cecece;
  grid-area: search; }

.Map {
  grid-area: map; }

.poll {
  grid-area: poll; }

.sign-up {
  grid-area: sign-up; }

.police {
  grid-area: police; }

.wccc {
  grid-area: wccc; }

.wcc {
  grid-area: wcc; }

.state-senate {
  grid-area: state-senate; }

.congress {
  grid-area: congress; }

.item.active {
  font-size: .85em;
  padding: .75em; }
  .item.active div {
    margin: auto; }
  .item.active input {
    max-width: 100%;
    border: none;
    padding: .5em; }
  .item.active button {
    padding: 1em 2em;
    border: none;
    background: #feb70d;
    font-weight: bold;
    cursor: pointer; }

.sign-up .box {
  position: relative; }
  .sign-up .box .phone-valid-alert, .sign-up .box .phone-invalid-alert, .sign-up .box .sending-alert {
    display: none;
    position: absolute;
    top: 0;
    padding: 1.5em; }
  .sign-up .box .sending-alert.active {
    width: 100%;
    padding: 2em;
    display: block;
    background: #feb70d; }
  .sign-up .box .phone-valid-alert.active {
    display: block;
    background: #9fd5b3; }
  .sign-up .box .phone-invalid-alert.active {
    display: block;
    background: #fd9a93; }
  .sign-up .box .close-phone-validation-alert {
    position: absolute;
    cursor: pointer;
    top: -.25em;
    right: -.25em;
    background: #fff;
    color: #f44e42;
    font-size: 1.5em;
    font-weight: bold;
    padding: .05em .4em; }

.important-dates {
  display: none; }

.important-dates.active {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex; }
  .important-dates.active div {
    background: #fff;
    text-align: center;
    padding: 1em;
    margin: auto; }

.item.active {
  border: 0.2em solid rgba(255, 255, 255, 0.5); }

@media all and (min-width: 950px) {
  .App {
    grid-template-columns: 33.33334%;
    grid-template-areas: "header header header" "search search search" "map map map" "sign-up sign-up poll" "congress state-senate council" "wccc wcc police"; }
  .sign-up {
    display: flex; }
    .sign-up .box {
      margin: auto; }
    .sign-up input {
      padding: 1em; } }

.Map {
  width: 100%; }
  .Map .mapboxgl-map {
    height: 100%; }

/* Basics */
.mapboxgl-ctrl-geocoder,
.mapboxgl-ctrl-geocoder *,
.mapboxgl-ctrl-geocoder *:after,
.mapboxgl-ctrl-geocoder *:before {
  box-sizing: border-box; }

.mapboxgl-ctrl-geocoder {
  font-family: 'Montserrat', sans-serif;
  position: relative;
  background-color: white;
  min-width: 240px;
  z-index: 1;
  border-radius: 3px; }

.mapboxgl-ctrl-geocoder input[type='text'] {
  border: none;
  background: #fff;
  font-size: 1.25em;
  width: 100%;
  height: 40px;
  margin: 0;
  padding: 10px 10px 10px 40px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

.mapboxgl-ctrl-geocoder input:focus {
  color: rgba(0, 0, 0, 0.75);
  outline: 0;
  box-shadow: none;
  outline: thin dotted\8; }

.mapboxgl-ctrl-geocoder .geocoder-icon-search {
  position: absolute;
  top: 10px;
  left: 10px; }

.mapboxgl-ctrl-geocoder button {
  padding: 0;
  margin: 0;
  background-color: #fff;
  border: none;
  cursor: pointer; }

.mapboxgl-ctrl-geocoder .geocoder-pin-right * {
  background-color: #fff;
  z-index: 2;
  position: absolute;
  right: 10px;
  top: 10px;
  display: none; }

/* Suggestions */
.mapboxgl-ctrl-geocoder ul {
  background-color: #fff;
  border-radius: 0 0 3px 3px;
  left: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  width: 100%;
  top: 100%;
  z-index: 1000;
  overflow: hidden;
  font-size: 12px; }

.mapboxgl-ctrl-bottom-left .mapboxgl-ctrl-geocoder ul,
.mapboxgl-ctrl-bottom-right .mapboxgl-ctrl-geocoder ul {
  top: auto;
  bottom: 100%; }

.mapboxgl-ctrl-geocoder ul > li > a {
  clear: both;
  cursor: default;
  display: block;
  padding: 5px 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #404040; }

.mapboxgl-ctrl-geocoder ul > li:last-child > a {
  border-bottom: none; }

.mapboxgl-ctrl-geocoder ul > li > a:hover {
  color: #202020;
  background-color: #f3f3f3;
  text-decoration: none;
  cursor: pointer; }

.mapboxgl-ctrl-geocoder ul > li.active > a {
  color: #202020;
  background-color: #e3e3e3;
  text-decoration: none;
  cursor: pointer; }

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg); } }

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

/* icons */
.geocoder-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  speak: none;
  background-repeat: no-repeat; }

.geocoder-icon-search {
  background-image: url(data:image/svg+xml;base64,); }

.geocoder-icon-close {
  background-image: url(data:image/svg+xml;base64,); }

.geocoder-icon-loading {
  background-image: url(data:image/svg+xml;base64,);
  -webkit-animation: rotate 400ms linear infinite;
  animation: rotate 400ms linear infinite; }

